export default{
	data(){
		return{
			isShow : false,
			jifen_text : '999积分',
			icon : '&#xe606;'
		}
	},
	methods:{
		off(){ //积分显示开关
			if(this.isShow){
				this.isShow = false;
				this.jifen_text = '999积分';
			}
			else{
				this.isShow = true;
				this.jifen_text = '';
			}
		},
		navTo(){
			uni.navigateTo({
				url: '../swop/swop'
			})
		}
	}
}
